import React from 'react';
import Collapse from 'react-bootstrap/esm/Collapse';

import { Link } from 'react-router-dom';



class Products extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            verIconoMasoMenos: false,
            fondoMenu: true,
            noActivo: true,

        };
        this.manejoRatonSobre = this.manejoRatonSobre.bind(this);
    }
    manejoRatonSobre() {
        if (!this.props.noActivo) {
            this.setState({ verIconoMasoMenos: !this.state.verIconoMasoMenos })
        }

    }

    render() {
        return (
            <>
                <div className="container col-5" style={{ minWidth: "452px" }} >
                    <div className="container" style={{ marginBottom: "20px", paddingLeft: "10px" }}>
                        <div className="row">
                            <Link className={this.props.noActivo ? 'disable-link' : ''} to={{ pathname: "/products/callinectes" }}  >
                                <img src={this.props.iconoProducto} alt={this.props.tituloProducto} height="80px" width="80px" />
                            </Link>


                            <Link to={{ pathname: "/products/callinectes" }}> 
                                <button className="custombtn"
                                    /*type="button"*/
                                    /*data-toggle="collapse"*/
                                    /*data-target={`#${this.props.idcollapse}`}*/

                                    aria-expanded={this.props.verIconoMasoMenos}

                                    aria-controls={this.props.idcollapse}
                                    onMouseEnter={this.manejoRatonSobre}
                                    onMouseLeave={this.manejoRatonSobre}
                                    disabled={this.props.noActivo}
                                    style={{ marginLeft: "5px", marginBottom: "10px", marginRight: "5px", marginTop: "5px", maxWidth: "330px", minWidth: "330px" }}>
                                    <span className="tituloproducto">{this.props.tituloProducto}</span>



                                    <Collapse in={this.state.verIconoMasoMenos}>
                                        <div id={this.props.idcollapse} >
                                            <div className="container p-0">

                                                <div >
                                                    <span className="fromtext">{this.props.fromText}</span>

                                                    <div className="btn-group">


                                                        <div className="customrow" >
                                                            <p className="card-text text-justify pr-1 pl-1 w-50 border-3 mb-0" ><h6 className="text-center font-weight-bold ">FEATURES</h6><span className="text text-left">{this.props.featureText}</span> </p>
                                                            <p className="card-text text-justify pr-1 pl-1 w-50"><h6 className="text-center font-weight-bold">BENEFITS</h6>{this.props.benefitText}</p>
                                                        </div>
                                                    </div>

                                                </div>
                                            </div>
                                        </div>
                                    </Collapse>


                                </button>
                            </Link>
                        </div>



                    </div>

                </div>
            </>

        )
    }
}

export default Products;