import React from 'react';
//import Menu from '../menu/Menu';
//import Footer from '../footer/Footer';
import Jumbotron from './jumbotron/Jumbotron';
import { Link } from 'react-router-dom';
import Izquierda from '../../img/izquierda.png';
import Derecha from '../../img/derecha.png';


class Phylosophy extends React.Component {

    render() {

        return (

            <>
                <div className="aboutusmain">
                <Link className="custom-link" to="/aboutus" >
                    <div className="menuflotanteizquierda">
                        <div>
                            <img className="iconoflotante" src={Izquierda} alt="izquierda" />
                        </div>
                        <div className="tituloflotante">about us</div>
                    </div>
                </Link>
                <Link className="custom-link" to="/products" >
                    <div className="menuflotantederecha">
                        <div className="tituloflotante">products</div>
                        <div>
                            <img className="iconoflotante" src={Derecha} alt="derecha" />
                        </div>
                    </div>
                </Link>
                    <Jumbotron />
                </div>

            </>
        )

    }

}

export default Phylosophy;