import React from 'react';
import Clock from '../../../img/clock.png';
import Mail from '../../../img/mail.png';
import Pin from '../../../img/pin.png';
import Phone from '../../../img/phone.png';

class Mapa extends React.Component {

  render() {

    return (
      <div>
        <div className="col" id="contenedorinfocontacto">
          <div className="rowcontact">
            <div ><img className="iconocontact" src={Clock} alt="Reloj" /> </div>
            <div className="contenidocontact">
              <p><span>Schedule</span><br />8:00 am to 9:00 pm</p>
            </div>
          </div>
          <div className="rowcontact">
            <div ><img className="iconocontact" src={Mail} alt="Email" /> </div>
            <div className="contenidocontact">
              <p><span>EMAIL</span> INFO@VIKINGFOOD.COM</p>
            </div>
          </div>
          <div className="rowcontact">
            <div ><img className="iconocontact" src={Pin} alt="Pin" /> </div>
            <div className="contenidocontact">
              <p><span>Lorem ipsum dolor sit amet</span> <br/>Consectetur adipisicing elit.</p>
            </div>
          </div>
          <div className="rowcontact">
            <div ><img className="iconocontact" src={Phone} alt="Phone" /> </div>
            <div className="contenidocontact">
              <p>0582 856 310<br/>Phone Call</p>
            </div>
          </div>
        </div>
        <div className="embed-responsive embed-responsive-16by9">

          <iframe title="geolocalizacion" className="embed-responsive-item" src="https://www.google.com/maps/embed?pb=!1m10!1m8!1m3!1d3921.7887091392604!2d-71.6208171!3d10.5956863!3m2!1i1024!2i768!4f13.1!5e0!3m2!1ses!2sve!4v1597266927740!5m2!1ses!2sve"></iframe>
        </div>
      </div>
    )

  }

}

export default Mapa;