const Menu = () => {
    const [defaultBackground, setDefaultBackground] = useState(false);
    var anterior = '/';
    var siguiente = '/aboutus';
    var titulosiguiente = 'aboutus';
    var tituloanterior = 'welcome';

    const location = useLocation();



    function handleDefaultBGI(defaultBackground, pvalor) {
        if (defaultBackground) {
            setDefaultBackground(true);
            //document.getElementsByClassName('fondomenu').style.backgroundImage = "url('"+banner+"')";

        }
        else {
            setDefaultBackground(false);
            //document.getElementsByClassName('fondomenu').style.backgroundImage = "url('none')";
        }
        switch (pvalor) {
            case 'home':
                anterior = '/';
                siguiente = '/aboutus';
                tituloanterior = 'welcome';
                titulosiguiente = 'about us';
                break;
            case 'about':
                anterior = '/';
                siguiente = '/phylosophy';
                tituloanterior = 'home';
                titulosiguiente = 'philosophy';
                break;
            case 'philo':
                anterior = '/aboutus';
                siguiente = '/produtcs';
                tituloanterior = 'about us';
                titulosiguiente = 'products';
                break;
            case 'products':
                anterior = '/phylosophy';
                siguiente = '/contactus';
                tituloanterior = 'philosophy';
                titulosiguiente = 'contact us';
                break;
            case 'contact':
                anterior = '/products';
                siguiente = '/';
                tituloanterior = 'products';
                titulosiguiente = 'home';
                break;
            default:
                break;

        }


        
    }

    return (
        <>
 
            <div className={defaultBackground ? "fondomenuverdad" : "fondomenufalso"}    >
                <div className="mi-menu">
                    <ul className="nav nav-pills nav-fill">
                        <li className="nav-item">
                            <a className="navbar-brand" href="/">
                                <img src={milogo} alt="Logo VikingFood" width="100" height="100" />
                            </a>

                        </li>
                        <li className="nav-item">

                            <Link className="custom-link" to="/" onClick={() => handleDefaultBGI(false, 'home')}>HOME</Link>
                        </li>
                        <li className="nav-item">

                            <Link className="custom-link" to="/aboutus" onClick={() => handleDefaultBGI(true, 'about')} >ABOUT US</Link>

                        </li>
                        <li className="nav-item">
                            <Link className="custom-link" to="/phylosophy" onClick={() => handleDefaultBGI(true, 'philo')}>PHILOSOPHY</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="custom-link" to="/products" onClick={() => handleDefaultBGI(true, 'products')}>PRODUCTS</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="custom-link" to="/contactus" onClick={() => handleDefaultBGI(true, 'contact')}>CONTACT</Link>
                        </li>
                    </ul>
                </div>
            </div>
            <div id="contenerdorslider" style={{ backgroundColor: defaultBackground ? 'white' : 'transparent' }}>
                <SlideRoutes location={location}   >
                    {/*aca van las paginas */}

                    <Route exact path='/' component={Home} />
                    <Route path='/aboutus' component={Aboutus} />
                    <Route path='/phylosophy' component={Phylosophy} />
                    <Route exact path='/products' component={Products} />
                    <Route exact path='/products/callinectes' component={Callinectes} />
                    <Route path='/contactus' component={Contactus} />



                </SlideRoutes>

            </div>

        </>
    )

}




export default Menu;