import React from 'react';

//import Menu from '../menu/Menu';
import Formulario from './formulario/Formulario';
import Mapa from './mapa/Mapa';
//import Footer from '../footer/Footer';
import Izquierda from '../../img/izquierda.png';
import { Link } from 'react-router-dom';

class Contacto extends React.Component {

      render() {

            return (

                  <>
                        <div className="contenedoraboutus">
                              <Link className="custom-link" to="/products" > 
                              <div className="menuflotanteizquierda">
                                   <div>
                                         <img  className="iconoflotante" src={Izquierda} alt="izquierda"/>
                                   </div>
                                   <div className="tituloflotante">products</div> 
                              </div>
                              </Link>

                              <main role="main" className="flex-shrink-0">

                                    <div className="container">
                                          <div className="container text-center">
                                                <h1 className="titulos">Contact</h1>
                                          </div>


                                          <div className="row" id="rowcontac">

                                                <div className="col-md-6">
                                                      <Mapa />

                                                </div>
                                                <div className="col-md-6">
                                                      <Formulario />

                                                </div>


                                          </div>
                                    </div>

                              </main>


                        </div>
                  </>

            )

      }

}

export default Contacto;