import React from 'react';
import '../../../App.css';


class Formulario extends React.Component {

  render() {

    return (

      <form className="mb-5">
        <div className="form-row" id="sendmessage">
          <div id="decoradorcontacto">   </div> <div id="titulocontacto">send Message</div> 
        </div>
        <div className="form-row">
          <div className="form-group m-0 w-50 pr-1">
            <label htmlFor="nya" className="textocontacto">Names </label>
            <input type="text" className="form-control-custom" id="nya" required autoComplete="off"/>

          </div>
          <div className="form-group  pr-1 m-0 w-50">
            <label htmlFor="email" className="textocontacto">Email</label>
            <input type="email" className="form-control-custom" id="email" required  autoComplete="off"/>
          </div>
        </div>



        <div className="form-row">
          <div className="form-group  pr-1 m-0 w-50">
            <label htmlFor="asunto" className="textocontacto">Subject</label>
            <input type="text" className="form-control-custom" id="asunto" required autoComplete="off"/>
          </div>

          <div className="form-group  pr-1 m-0 w-50">
            <label htmlFor="telefono" className="textocontacto">Phone</label>
            <input type="text" className="form-control-custom" id="telefono" required autoComplete="off" ></input>
          </div>

        </div>
        
        <div className="form-group">
          <label htmlFor="mensaje" className="textocontacto">Message</label>
          <textarea className="form-control-custom" id="mensaje" required></textarea>
        </div>

        <div className="text-center mt-4">
        <button type="submit" className="btnsubmit">submit</button>
        </div>
        

      </form>

    )

  }

}

export default Formulario;